body {
  padding-top: 5rem;
}

html {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-size: 10px;
  line-height: 1.5;
  font-weight: 400;
  color: #333742;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  font-weight: 700;
}

h1, .h1 {
  font-size: 3.8em;
}

h2, .h2 {
  font-size: 3.2em;
}

h3, .h3 {
  font-size: 2.8em;
}

h4, .h4 {
  font-size: 2.2em;
}

h5, .h5 {
  font-size: 1.8em;
}

h6, .h6 {
  font-size: 1.6em;
}

p {
  font-size: 1.6em;
}

.text-primary {
  color: #04a9f4;
}

.text-success {
  color: #00c854;
}

.text-warning {
  color: #ffd500;
}

.text-info {
  color: #95e9e2;
}

.text-danger {
  color: #ff5723;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  visibility: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.navbar-left {
  position: relative;
  width: 100%;
  overflow-x: hidden;
  z-index: 10001;
}

.navbar-left__header {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 5rem;
  border-bottom: 1px solid #959fb0;
  background: #414550;
  padding: 15px 15px;
}

.navbar-left__toggler {
  display: inline-block;
  width: 2.5rem;
  height: 2rem;
  background: transparent;
  border: 0;
  text-align: right;
  padding: 0;
  border-radius: 0.3em;
  cursor: pointer;
}

.navbar-left__toggler:hover, .navbar-left__toggler:focus {
  outline: 0;
}

.navbar-left__toggler .fa-bars {
  font-size: 1.6em;
  color: whitesmoke;
}

.navbar-left__menu {
  position: fixed;
  left: 0;
  top: 5rem;
  width: 100%;
  height: 100%;
  background: #414550;
  transform: translateX(-100%);
  overflow-y: scroll;
  transition: transform 0.35s ease;
  max-width: 32.5rem;
}

.navbar-left__menu.open {
  transform: translateX(0);
}

.navbar-left__menu ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.navbar-left__menu::-webkit-scrollbar {
  width: .8rem;
}

.navbar-left__menu::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-border-radius: 10px;
  border-radius: 10px;
}

.navbar-left__menu::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: rgba(255, 255, 255, 0.8);
}

.navbar-left__menu-item {
  position: relative;
  text-align: left;
  color: #f2f5f9;
}

.navbar-left__menu-item:hover, .navbar-left__menu-item:focus {
  color: #f2f5f9;
}

.navbar-left__menu-item.--has-dropdown {
  position: relative;
}

.navbar-left__menu-item.--has-dropdown::before {
  content: "\f105";
  position: absolute;
  top: 1.9rem;
  right: 1rem;
  transform: translateY(-50%);
  display: block;
  font-family: FontAwesome;
  font-size: inherit;
}

.navbar-left__menu-item-divider {
  padding: 7.5px 15px;
  margin-top: 15px;
  text-transform: uppercase;
  font-weight: 700;
  color: #959fb0;
  border-bottom: 1px solid #959fb0;
}

.navbar-left__menu-link {
  display: block;
  padding: 7.5px 15px;
  text-decoration: none;
  font-size: 1.6em;
  color: inherit;
}

.navbar-left__menu-link .fa {
  color: inherit;
  margin-right: 1rem;
}

.navbar-left__menu-link:hover, .navbar-left__menu-link:focus {
  background: #04a9f4;
}

.sub-menu {
  display: block;
  transition: height .3s ease;
}

.btn {
  position: relative;
  display: inline-block;
  font-family: inherit;
  font-size: 1.6em;
  padding: .5rem 1.5em;
  border: .4rem solid transparent;
  text-decoration: none;
  margin-bottom: 1rem;
}

.btn--primary {
  color: #fff;
  background-color: #04a9f4;
  border-color: #04a9f4;
}

.btn--primary:focus {
  border-color: #0386c2;
}

.btn--secondary {
  color: #fff;
  background-color: #8fa4ad;
  border-color: #8fa4ad;
}

.btn--secondary:focus {
  border-color: #728c97;
}

.btn--success {
  color: #fff;
  background-color: #00c854;
  border-color: #00c854;
}

.btn--success:focus {
  border-color: #00953f;
}

.btn--danger {
  color: #fff;
  background-color: #ff5723;
  border-color: #ff5723;
}

.btn--danger:focus {
  border-color: #ef3800;
}

.btn--warning {
  color: #000;
  background-color: #ffd500;
  border-color: #ffd500;
}

.btn--warning:focus {
  border-color: #ccaa00;
}

.btn--info {
  color: #fff;
  background-color: #95e9e2;
  border-color: #95e9e2;
}

.btn--info:focus {
  border-color: #6be0d6;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  min-width: 15rem;
  display: none;
  padding-top: .4rem;
  padding-bottom: .4rem;
  background: #fff;
  transform: translateY(0.5rem);
  border: 1px solid #cccccc;
  box-shadow: 0.3rem 0.3rem 0 rgba(0, 0, 0, 0.2);
  z-index: 1000;
}

.dropdown-menu.show {
  display: inline-block;
}

.dropdown-item {
  display: block;
  padding: .4rem 1rem;
  font-size: 1.6em;
  color: #6c757d;
  background: #fff;
  text-decoration: none;
}

.dropdown-item:hover, .dropdown-item:focus {
  background: #f3f4f5;
}

.dropdown-item:active {
  color: #fff;
  background: #04a9f4;
}

.dropdown-divider {
  width: 100%;
  height: 1px;
  border: 0;
  margin-top: .3rem;
  margin-bottom: .3rem;
  background: #d8dbdd;
}

.dropdown-toggler {
  cursor: pointer;
  z-index: 1;
}

.alert {
  position: relative;
  display: block;
  width: 100%;
  padding: 1rem 2rem;
  border-radius: .3rem;
  border: 1px solid transparent;
  margin-bottom: 1rem;
}

.alert--primary {
  color: #0386c2;
  background: rgba(4, 169, 244, 0.08);
  border-color: #04a9f4;
}

.alert--secondary {
  color: #728c97;
  background: rgba(143, 164, 173, 0.08);
  border-color: #8fa4ad;
}

.alert--success {
  color: #00953f;
  background: rgba(0, 200, 84, 0.08);
  border-color: #00c854;
}

.alert--info {
  color: #6be0d6;
  background: rgba(149, 233, 226, 0.08);
  border-color: #95e9e2;
}

.alert--warning {
  color: #ccaa00;
  background: rgba(255, 213, 0, 0.08);
  border-color: #ffd500;
}

.alert--danger {
  color: #ef3800;
  background: rgba(255, 87, 35, 0.08);
  border-color: #ff5723;
}

.alert--dismissible {
  padding-right: 5rem;
}

.alert-close {
  position: absolute;
  top: 0;
  right: 0;
  padding: 1rem 1.8rem;
  border: none;
  background: transparent;
  font-size: 2rem;
  font-weight: bold;
  opacity: .5;
  cursor: pointer;
}

.alert-close:hover, .alert-close:focus {
  opacity: 1;
}

.alert--hidden {
  display: none;
}
